<template>
  <div class="showView">
    <BlogItem v-for="item in listArr" :key="item.id" :itemObj="item" />
    <div class="pageGroup">
      <div class="btn btnLeft" @click="btnLeft">&lt; 上一页</div>
      <div class="btn btnRit" @click="btnRit">下一页 &gt;</div>
    </div>

    <div class="toast" v-show="showToast">数据加载中...</div>
  </div>
</template>

<script>
import BlogItem from './BlogItem.vue'

export default {
  data () {
    return {
      page: 1,
      listArr: [],
      showToast: false
    }
  },
  components: {
    BlogItem
  },
  created () {
    this.getData()
  },
  methods: {
    // 点击上一页的按钮
    btnLeft () {
      if (this.page <= 1) {
        alert('已经是第一页了')
        return
      }
      this.showToast = true
      this.page--
      this.getData()
      this.scrTop()
    },
    // 点击下一页的按钮
    btnRit () {
      if (this.listArr.length < 9 || this.listArr.length == 0) {
        alert('已经没有更多了')
        return
      }
      this.showToast = true
      this.page++
      this.getData()
      this.scrTop()
    },
    // 返回顶部的函数
    scrTop () {
      if (document.body.scrollTop) {
        document.body.scrollTop = 0
      } else {
        document.documentElement.scrollTop = 0
      }
    },
    // 获取请求的函数
    getData () {
      this.axios({
        url: 'https://ku.qingnian8.com/dataApi/blog/showBlog.php',
        params: {
          page: this.page
        }
      }).then((res) => {
        this.listArr = res.data
        this.showToast = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
.toast {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  color: #fff;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.showView {
  padding: 30px 30px;
}
.pageGroup {
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  .btn {
    border: pink 1px solid;
    padding: 10px 20px;
    color: pink;
    font-size: 18px;
    margin: 0 10px;
    cursor: pointer;
    transition: 0.3s;
    &:hover {
      background: pink;
      color: #fff;
    }
  }
}
</style>
